.menu-table {
  margin: 0;
  padding: 0 20px;
  padding-top: 30px;
  padding-bottom: 30px;
  box-sizing: border-box;
}

.menu-table__title {
  position: relative;
  margin: 0;
  margin-bottom: 40px;
  padding: 0;
  font-family: "Arsenal", sans-serif;
  font-size: 25px;
  text-align: center;
  text-transform: uppercase;

  &::after {
    content: "";
    background-color: @main-yellow;
    width: 32px;
    height: 3px;
    position: absolute;
    border-radius: 30px;
    bottom: -15px;
    left: 125px;
  }
}

.menu-table__subheading {
  margin: 0;
  margin-bottom: 15px;
  margin-top: 20px;
  padding: 0 20px;
  text-align: center;
  font-size: @mob-subheading-font-size;
  line-height: @mob-subheading-line-height;
  font-weight: bold;
}

.menu__type {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
  position: relative;

  & button {
    background-color: transparent;
    border: none;
    outline: none;
    font-family: @main-font;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 12px;
  }

  & li :hover {
    color: @text-opacity-grey;
  }
}

.menu__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: none;
}

.menu__current {
  display: block;
}
