.team {
  margin: 0;
  padding: 0;
  padding-top: 40px;
  color: @main-white;
  background-color: @bg-color;
  box-sizing: border-box;
  background-image: url("../img/bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.team__before-title {
  text-align: center;
  margin: 0;
  margin-bottom: 20px;
  text-transform: uppercase;
  font-weight: normal;
  font-size: 10px;
}

.team__title {
  font-size: 25px;
  position: relative;
  margin: 0;
  margin-bottom: 40px;
  padding: 0;
  font-family: "Arsenal", sans-serif;
  text-align: center;
  text-transform: uppercase;

  &::after {
    content: "";
    background-color: @main-yellow;
    width: 32px;
    height: 3px;
    position: absolute;
    border-radius: 30px;
    bottom: -15px;
    left: 145px;
  }
}

.team__subheading {
  margin: 0;
  margin-bottom: 15px;
  padding: 0 20px;
  text-align: center;
  font-size: @mob-subheading-font-size;
  line-height: @mob-subheading-line-height;
  font-weight: bold;
}

.team__text {
  margin: 0;
  margin-bottom: 30px;
  padding: 0 20px;
  text-align: center;
  font-size: @paragraph-font-size;
  line-height: @paragraph-line-height;
}

.team__img {
  padding: 0;
  margin: 0;
  display: block;
}
