.header {
  position: relative;
  background-color: @bg-color;
}

.header__nav {
  padding-top: 50px;
  font-size: 20px;
  line-height: 22px;
  text-align: center;
  width: auto;
}

.header__nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  width: 100%;

  & li {
    padding-bottom: 15px;
  }

  & a {
    width: 100%;
    color: @main-white;
    text-transform: uppercase;
    text-decoration: none;
    border-bottom: 1px solid @main-white;
  }
}

.header__nav-toggle {
  position: absolute;
  display: block;
  z-index: 2;
  top: 0;
  right: 0;
  border: none;
  outline: none;
  width: 40px;
  height: 40px;
  background-color: transparent;
  cursor: pointer;
}

.header__nav--closed {
  background-color: transparent;
  position: absolute;
  z-index: 4;
  top: 0;
  right: 0;
}

.header__nav--closed .header__nav-toggle::before {
  content: "";
  position: absolute;
  top: 10px;
  left: 8px;
  width: 25px;
  height: 2px;
  background-color: @main-yellow;
  box-shadow: 0 8px 0 0 @main-yellow, 0 16px 0 0 @main-yellow;
}

@media (max-width: @mobile-width-only) {
  .header__nav--closed .site-list__item {
    display: none;
  }
}

.header__nav--opened .header__nav-toggle {
  top: 0;
  right: 0;
  left: auto;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 20px;
    left: 5px;
    width: 36px;
    height: 2px;
    background-color: @main-yellow;
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }

  &:active::before,
  &:active::after {
    background-color: fade(@main-yellow, 30%);
  }
}
