.promo {
  padding-top: 50px;
  font-family: "Open Sans", sans-serif;
  background-color: @bg-color;
  background-image: url("../img/header-bg.jpg");
  background-position: center;
  background-size: cover;
  color: @main-white;
  text-align: center;
  padding-bottom: 20px;
}

.promo__title {
  margin: 0;
  margin-top: 40px;
  padding: 0 20px;
  text-align: center;
  font-size: 10px;
  line-height: @mob-subheading-line-height;
  text-transform: uppercase;
  font-weight: normal;
}

.promo__logo {
  margin-top: 10px;
  margin-bottom: 44px;
}

.logo {
  position: absolute;
  width: 55px;
  height: 55px;
  top: 10px;
  left: 132px;
}

.promo__button {
  box-sizing: border-box;
  margin: 0;
  font-family: @main-font;
  text-decoration: none;
  color: @main-white;
  padding: 9px 20px;
  font-size: 8px;
  line-height: 20px;
  text-transform: uppercase;

  border-radius: 5px;
  font-weight: 700;

  &--yellow {
    margin-right: 12px;
    background-color: @main-yellow;
  }

  &--grey {
    background-color: transparent;
    border: 1px solid @border-opacity-grey;
  }
}

.promo__time {
  margin: 0;
  margin-top: 44px;
  margin-bottom: 20px;
  font-size: 8px;
  line-height: 8px;
}

.promo__list {
  list-style: none;
  margin: 0;
}
