.about {
  margin: 0;
  margin-top: 30px;
}

.about__title {
  position: relative;
  margin: 0;
  margin-bottom: 40px;
  padding: 0;
  font-family: "Arsenal", sans-serif;
  font-size: 25px;
  text-align: center;
  text-transform: uppercase;

  &::after {
    content: "";
    background-color: @main-yellow;
    width: 32px;
    height: 3px;
    position: absolute;
    border-radius: 30px;
    bottom: -15px;
    left: 145px;
  }
}

.about__subheading {
  margin: 0;
  margin-bottom: 15px;
  padding: 0 20px;
  text-align: center;
  font-size: @mob-subheading-font-size;
  line-height: @mob-subheading-line-height;
  font-weight: bold;
}

.about__text {
  margin: 0;
  margin-bottom: 30px;
  padding: 0 20px;
  text-align: center;
  font-size: @paragraph-font-size;
  line-height: @paragraph-line-height;
}

.about__img {
  padding: 0;
  margin: 0;
  display: flex;
  max-height: 290px;
  width: 100%;
}
