.parties {
  margin: 0;
  padding: 0;
  padding-top: 30px;
  padding-bottom: 20px;
  box-sizing: border-box;
  background-color: @bg-color;
  color: @main-white;
  background-image: url("../img/specialty's-bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.parties__title {
  text-align: center;
  margin: 0;
  margin-bottom: 20px;
  text-transform: uppercase;
  font-weight: normal;
  font-size: 10px;
}

.parties__list {
  padding: 0;
  margin: 0;
}

.parties__img {
  display: flex;
  width: 100%;
  max-height: 330px;
  margin-top: 20px;
}

.parties__text {
  color: @border-opacity-grey;
  margin: 0;
  margin-top: 30px;
  padding: 0 20px;
  text-align: center;
  font-size: @paragraph-font-size;
  line-height: @paragraph-line-height;

  & a {
    text-decoration: none;
    color: inherit;
  }
}
